/*Header*/
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;

  background-color: #282c34;
  color: white;
  padding: 25px 0;

  font-family: 'Montserrat', sans-serif;
}

.navWrap {
  height: 95px;
}

.nameAuthor {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 21px;
  letter-spacing: 2px;
  color: white;
  padding: 7px;
}

.nameAuthor:hover {
  color: white;
  text-decoration: none;
}

.menuItem {
  padding: 15px 20px;

  font-weight: 400;
  font-size: 15px;
  text-transform: uppercase;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.carouselContainer {
  
}

/*About me*/
.aboutMe {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #f2f2f2;
}

.centered {
  text-align: center;
}

.row {
  margin-left: 0;
  margin-right: 0;
}

.avatar {
  border-radius: 150px;
  width: 200px;
  height: 200px;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}